import {
	        mapState,  
	        mapMutations  
	    } from 'vuex';  

var util = require('@/common/util.js');
export default {
	data() {
		return {
			loadModal:false,
			modalName: null,
			reportContent: '',
			dismissContent: '',
			detailInfo: {},
			statusList: [
				{ label: '待审核', value: 0, class: 'blue'},
				{ label: '通过', value: 10, class: 'green'},
				{ label: '未通过(拒绝)', value: 20, class: 'red'},
				{ label: '取消(退团)', value: 30, class: 'gray'}
			],
			dotStyle: false,
			userInfo: {},
			activeTab: null,
			id: ''
			
		}
	},
	onShow() {
		if (this.id) {
			this.getDetailInfo(this.id)
		}
	},
	onLoad(options) {
		this.id = options.id
		this.activeTab = options.activeTab
		this.getDetailInfo(this.id)
		uni.getStorage({
			key: 'userInfo',
			success: (res)=>{
				this.userInfo = JSON.parse(res.data);
				if ((!this.userInfo) || (!this.userInfo.user_id)) {
					this.$store.dispatch('getuserinfo')
				}
			}
		});
	},
	computed: {
	    ...mapState(['emptyRoomPic'])  
	},
	
	methods: {
		goChat(item) {
			uni.navigateTo({
				url: `/pages/chat/detail?id=${item.user_id}`
			});
		},
		cancelJoin() {
			this.$api.getCancelPersonal({id: this.detailInfo.id}).then(res => {
				uni.showToast({
					icon: 'success',
					title: '操作成功'
				});
				this.getDetailInfo(this.id)
			}).catch(err => {
			})
		},
			// 状态处理
		handleStatus (status,id){
			let params = {
				group_id: this.detailInfo.id,
				member_id: id,
				status: status
			}
			this.$api.getPersonalExamine(params).then(res => {
				uni.showToast({
					icon: 'success',
					title: '操作成功'
				});
				this.getDetailInfo(this.id)
			}).catch(err => {
			})
		},
		
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		
		hideModal(e) {
			this.modalName = null
		},
		
		textareaAInput(e) {
			this.reportContent = e.detail.value
		},
		
		reported() {
			this.$api.getGroupReported({
				id: this.detailInfo.id,
				content: this.reportContent
			}).then(res => {
				this.modalName = null
				 uni.showToast({
					 icon: 'none',
					 title: '举报成功,我们会尽快进行核实'
				 });
			}).catch(err => {
				this.modalName = null
			})
		},
		
		BackPage() {
			uni.navigateBack({
				delta: 1
			});
		},
		
		getDetailInfo (id) {
			this.loadModal = true
			this.$api.getPersonalDetail({
				id: id,
			}).then(res => {
			   this.loadModal = false
			   res.data.cotenant_list  && res.data.cotenant_list.forEach((item) => {
				   if (item && item.end_time) {
					  item.end_time = util.format(item.end_time)
				   }
			   })
			   res.data.chamber_img_url = res.data.chamber_img_url || this.emptyRoomPic
			   res.data.imgUrlList = res.data.chamber_img_url.split(',')
			   res.data.created_time = util.getDate(res.data.created_time)
			   this.detailInfo = res.data
			}).catch(err => {
			})
		},
		
		dismissTextareaAInput (e) {
			this.dismissContent = e.detail.value
		},
		
		// doDismiss () {
		// 	uni.showModal({
		// 		title: '温馨提示',
		// 		content: '确定解散该合租团吗？',
		// 		confirmText: '确定',
		// 		cancelText: '再想想',
		// 		success: (res)=>{
		// 			if (res.confirm) {
		// 				this.doDismissAjax()
		// 			} else if (res.cancel) {
		// 			}
		// 		}
		// 	});	
		// },
		
		doDismissAjax () {
			this.$api.getPersonaDismiss({
				id: this.detailInfo.id,
				dismissContent: this.dismissContent // 解散理由
			}).then(res => {
				uni.showToast({icon: 'none',title: '解散成功'});
				setTimeout(() => {
					uni.switchTab({
					    url: '/pages/home/home',
					    success: function(e) {
					     uni.setStorage({
					         key: 'hometype',
					         data: '2'
					     });
					    }
					});
				}, 500)
			}).catch(err => {
			})
		},
		
		operation (){
			uni.showModal({
				title: '温馨提示',
				content: '是否同意该申请？',
				confirmText: '通过',
				cancelText: '拒绝',
				success: (res)=>{
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});	
		}
	}
}
